@charset "UTF-8";
@import "https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap";
@import "https://fonts.googleapis.com/css2?family=Oswald:wght@600&display=swap";

/* css-side-effects-plugin-ns:app/components/Footer/footer.css */
.footer {
  background: var(--main_color);
  padding: 30px 0px;
  text-align: center;
}
.footer .row {
  width: 100%;
  margin: 1% 0%;
  padding: 0.6% 0%;
  color: gray;
  font-size: 0.8em;
}
.footer .row a {
  text-decoration: none;
  color: gray;
  transition: 0.5s;
}
.footer .row a:hover {
  color: #000;
}
.footer .row ul {
  width: 100%;
}
.footer .row ul li {
  display: inline-block;
  margin: 0px 30px;
}
.footer .row a i {
  font-size: 2em;
  margin: 0% 1%;
}
@media (max-width:720px) {
  .footer {
    text-align: left;
    padding: 5%;
  }
  .footer .row ul li {
    display: block;
    margin: 10px 0px;
    text-align: left;
  }
  .footer .row a i {
    margin: 0% 3%;
  }
}

/* css-side-effects-plugin-ns:app/styles.css */
:root {
  --orange_color: #da722c;
  --dark-blue_color: #023047;
  --light_blue_color: #6f7dfb;
  --light_color: #e8f7fb;
  --dark_color: #070917;
  --main_color: #fef7e5;
  --accent_color: #fc5356;
  --heading_color: #20247b;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  transition: all 0.4s ease;
}
.text-green {
  color: green;
}
.text-red {
  color: red;
}
.text-white {
  color: white;
}
.section {
  padding: 100px 0;
  position: relative;
}
.section-title {
  padding-bottom: 45px;
  padding: 1rem;
  margin-bottom: 1rem;
}
.flex {
  display: flex;
}
.section-title h1 {
  font-weight: 700;
  color: var(--heading_color);
  font-size: 2.5rem;
  margin: 0 auto;
  border-left: 5px solid var(--accent_color);
  padding-left: 15px;
}
.section-title h2 {
  font-weight: 600;
  color: var(--heading_color);
  font-size: 2rem;
  margin: 0 0 15px;
  border-left: 5px solid var(--accent_color);
  padding-left: 15px;
}
.section-title h3 {
  font-weight: 500;
  color: var(--heading_color);
  font-size: 1.5rem;
  margin: 0 0 15px;
  border-left: 5px solid var(--accent_color);
  padding-left: 15px;
}
.section-title p {
  margin: 10px 0;
}
.section-title ul {
  margin-left: 3rem;
}
.lr-container,
.lr-container-right {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  margin-bottom: 1em;
  margin: 0 auto;
  width: 90%;
}
w-100 {
  width: 100;
}
w-45 {
  width: 45%;
}
.lr-container img,
.lr-container-right img {
  max-width: 45%;
  margin-bottom: 1rem;
  object-fit: contain;
  align-self: flex-start;
  overflow: clip;
  overflow-clip-margin: content-box;
}
.shadow {
  box-shadow: -2px -1px 15px 16px rgba(0, 0, 0, 0.75);
}
.lr-content {
  display: block;
  width: 45%;
  margin: 0 4em;
}
.lr-content p {
  font-size: 1.1em;
  line-height: 1.5em;
}
.container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  flex-flow: wrap;
  width: 90%;
  margin: 50px auto;
  gap: 30px;
}
.heading {
  text-align: center;
  font-size: 30px;
  margin-bottom: 50px;
}
.mt {
  margin-top: 20px;
}
.nav-container {
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}
.nav-container img {
  height: 55px;
  width: auto;
}
.navbar {
  height: 60px;
  background-color: var(--main_color);
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 99;
}
.lr-left {
  width: 450px;
}
.nav-elements ul {
  display: flex;
  justify-content: space-between;
  list-style-type: none;
}
.nav-elements ul li:not(:last-child) {
  margin-right: 60px;
}
.nav-elements ul a {
  font-size: 16px;
  font-weight: 400;
  color: #2f234f;
  text-decoration: none;
}
.nav-elements ul a:hover {
  color: #000;
  font-weight: 700;
  transition: all 0.3s ease-in;
}
.nav-elements ul a.active {
  color: #574c4c;
  font-weight: 500;
  position: relative;
}
.nav-elements ul a.active::after {
  content: "";
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #574c4c;
}
.wrapper {
  max-width: 100%;
  margin: 0 auto;
  margin-top: 4rem;
}
header {
  background: linear-gradient(180deg, #6f7dfb 0%, #26308c 100%);
  height: 100vh;
  width: 100%;
}
nav .logo {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 900;
  font-size: 2rem;
  color: #000;
}
.logoBody {
  transform: translateZ(55px);
  padding: 15px;
  border-radius: 25px;
  border: 2px solid #ff00ff;
  font-size: 20px;
  color: #fff;
  background: #000;
}
.hamburger-img {
  width: auto;
  height: 40px;
}
.menu-icon {
  display: none;
}
@media (max-width: 600px) {
  .lr-container {
    flex-direction: column;
  }
  .lr-container-right {
    flex-direction: column-reverse;
  }
  .lr-container img,
  .lr-container-right img {
    max-width: 100%;
    align-self: center;
  }
  .lr-content {
    margin: 0 auto;
    width: 100%;
  }
  .menu-icon {
    display: block;
    cursor: pointer;
  }
  .nav-elements {
    position: absolute;
    right: 0;
    top: 60px;
    background-color: #fef7e5;
    width: 0px;
    height: calc(100vh - 60px);
    transition: all 0.3s ease-in;
    overflow: hidden;
    z-index: 1000;
  }
  .nav-elements.active {
    width: 270px;
  }
  .nav-elements ul {
    display: flex;
    flex-direction: column;
  }
  .nav-elements ul li {
    margin-right: unset;
    margin-top: 22px;
  }
}
.hero-section {
  display: flex;
  height: 100vh;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  padding-top: 60px;
}
.hero-section .left h1 {
  font-family: "Raleway", sans-serif;
  font-weight: 900;
  font-size: 50px;
  line-height: 125%;
  color: #FFF;
  margin-bottom: 0;
}
.hero-section .left p {
  font-family: "Poppins", sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 200%;
  color: #FFF;
  margin-bottom: 30px;
}
.hero-section .left {
  width: 457px;
}
.hero-section .right img {
  height: 520px;
  vertical-align: middle;
}
.hero-section .right {
  align-self: flex-end;
}
.achievement-cards {
  display: none;
}
.achievement-card {
  position: absolute;
}
.achievement-card .content {
  border-radius: 24px;
  background: #fff;
  padding: 8px 32px;
}
.achievement-card h3 {
  font-family: "Poppins", sans-serif;
  font-weight: bold;
  font-size: 36px;
  color: var(--dark-blue-color);
  margin: 0;
}

/* css-side-effects-plugin-ns:app/components/Services/services.css */
body {
  margin-top: 20px;
}
.row {
  margin-left: 2rem;
}
.feature-box-1 {
  padding: 32px;
  box-shadow: 0 0 30px rgba(31, 45, 61, 0.125);
  margin: 15px 0;
  position: relative;
  z-index: 1;
  border-radius: 10px;
  overflow: hidden;
  -moz-transition: ease all 0.35s;
  -o-transition: ease all 0.35s;
  -webkit-transition: ease all 0.35s;
  transition: ease all 0.35s;
  top: 0;
  max-width: 300px;
}
.feature-box-1 * {
  -moz-transition: ease all 0.35s;
  -o-transition: ease all 0.35s;
  -webkit-transition: ease all 0.35s;
  transition: ease all 0.35s;
}
.feature-box-1 .icon {
  width: 70px;
  height: 70px;
  line-height: 70px;
  background: #fc5356;
  color: #ffffff;
  text-align: center;
  border-radius: 50%;
  margin-bottom: 22px;
  font-size: 27px;
}
.feature-box-1 .icon i {
  line-height: 70px;
}
.feature-box-1 h5 {
  color: #20247b;
  font-weight: 600;
}
.feature-box-1 p {
  margin: 0;
}
.feature-box-1:after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: auto;
  right: 0;
  border-radius: 10px;
  width: 0;
  background: #20247b;
  z-index: -1;
  -moz-transition: ease all 0.35s;
  -o-transition: ease all 0.35s;
  -webkit-transition: ease all 0.35s;
  transition: ease all 0.35s;
}
.feature-box-1:hover {
  top: -5px;
}
.feature-box-1:hover h5 {
  color: #ffffff;
}
.feature-box-1:hover p {
  color: rgba(255, 255, 255, 0.8);
}
.feature-box-1:hover:after {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  left: 0;
  right: auto;
}

/* css-side-effects-plugin-ns:app/components/Contact/contact.css */
main {
  background: white;
}
main > * {
  position: relative;
  transition: all 0.3s 0.3s cubic-bezier(0.5, 0, 0.5, 1);
  transition-property: opacity, transform;
}
main > *[aria-hidden=true] {
  display: initial;
  pointer-events: none;
  transition-delay: 0s;
  opacity: 0;
  transform: scale(0.8);
}
.contact-container {
  width: 100%;
  margin-top: 5rem;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
.contact-wrapper {
  width: 100%;
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 0px 21px 41px -13px rgba(0, 0, 0, 0.18);
}
.contact-wrap {
  background: #fff;
  width: 100%;
  padding: 3rem;
}
.contact-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.contact-left {
  flex: 0 0 50%;
  max-width: 50%;
}
.contact-right {
  display: flex;
  flex: 0 0 50%;
  max-width: 50%;
  align-items: stretch !important;
}
.info-wrap {
  background-image: url(/build/_assets/contactus-J53LQT5G.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  width: 100%;
}
textarea.form-control {
  height: 110px;
}
.contact-address-text {
  flex: 0 0 33.33%;
  max-width: 33.33%;
}
.mb-4 {
  margin-bottom: 1.5rem !important;
}
#form-message-warning {
  color: red;
}
#form-message-success {
  color: green;
  font-size: 18px;
  font-weight: 500;
}
.contactForm .form-control {
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  padding: 0;
}
#contactForm .error {
  color: red;
  font-size: 12px;
}
#contactForm .form-control {
  font-size: 16px;
}
.form-group {
  flex: 0 0 100%;
  max-width: 100%;
  margin-bottom: 1rem;
}
.dbox {
  width: 100%;
  margin-bottom: 25px;
}
.dbox p span {
  font-weight: 400;
  color: rgba(0, 0, 0, 0.2);
  display: block;
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 600;
}
.dbox p a {
  color: rgba(0, 0, 0, 0.7);
}
.dbox .text {
  width: 100%;
}
.form-control {
  display: block;
  width: 100%;
  height: calc(1.5em + 0.75rem + 2px);
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  -webkit-transition: border-color 0.15s ease-in-out, -webkit-box-shadow 0.15s ease-in-out;
  transition: border-color 0.15s ease-in-out, -webkit-box-shadow 0.15s ease-in-out;
  -o-transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  transition:
    border-color 0.15s ease-in-out,
    box-shadow 0.15s ease-in-out,
    -webkit-box-shadow 0.15s ease-in-out;
}
.submitting {
  width: 100%;
  padding: 10px 0;
  font-size: 16px;
  font-weight: 500;
  transition: color 2s ease-in-out, -webkit-box-shadow 2s ease-in-out;
  text-align: center;
}
.btn {
  padding: 12px 16px;
  cursor: pointer;
  border-width: 1px;
  border-radius: 5px;
  font-size: 14px;
  font-weight: 400;
  -webkit-box-shadow: 0px 10px 20px -6px rgba(0, 0, 0, 0.12);
  -moz-box-shadow: 0px 10px 20px -6px rgba(0, 0, 0, 0.12);
  box-shadow: 0px 10px 20px -6px rgba(0, 0, 0, 0.12);
  position: relative;
  margin-bottom: 20px;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}
.btn-primary {
  color: #fff;
  background-color: #007bff;
  border-color: #007bff;
}
.btn.btn-primary {
  background: #e3b04b !important;
  border-color: #e3b04b !important;
  color: #fff;
}
.social-media {
  width: 100%;
}
.social-media h3 {
  font-size: 18px;
  line-height: 1.5;
  font-weight: 400;
  color: 000;
}
.social-media p a {
  color: rgba(0, 0, 0, 0.2);
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 500;
  margin-right: 10px;
  transition: .3s all ease;
}
.social-media p a:hover {
  color: #000;
}
.completed {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  transition: all 3s 1s;
  min-height: 400px;
}
.completed h3 {
  font-size: 20px;
  line-height: 1.5;
  font-weight: 700;
  color: 000;
}
@media (min-width: 768px) {
  .dbox {
    margin-bottom: .5rem;
  }
}
@media (max-width: 768px) {
  .contact-wrap {
    padding: 1.5rem;
  }
  .contact-row {
    flex-direction: column;
  }
  .contact-left,
  .contact-right {
    max-width: 100%;
  }
  .info-wrap {
    height: auto;
    min-height: 400px;
  }
}

