/* css-side-effects-plugin-ns:app/components/Card/card.css */
.card {
  height: 300px;
  min-width: 300px;
  width: 325px;
  border-radius: 5px;
  border: 1px solid #ccc;
  box-shadow:
    rgba(0, 0, 0, 0.4) -5px 5px,
    rgba(0, 0, 0, 0.3) -10px 10px,
    rgba(0, 46, 170, 0.2) -15px 15px,
    rgba(0, 0, 0, 0.1) -10px 10px,
    rgba(0, 0, 0, 0.05) -0 0;
  transition: 0.3s;
  text-align: center;
  padding: 1em;
}
.card img {
  height: 100px;
  width: auto;
  margin: 0 auto;
}
.card:hover {
  transform: scale(1.1);
  box-shadow:
    rgba(0, 0, 0, 0.4) -5px 5px,
    rgba(0, 0, 0, 0.3) -10px 10px,
    rgba(0, 46, 170, 0.2) -15px 15px,
    rgba(0, 0, 0, 0.1) -10px 10px,
    rgba(0, 0, 0, 0.05) -0 0;
}
@media screen and (max-width: 1000px) {
  .card {
    width: 40%;
  }
}
@media screen and (max-width: 620px) {
  .container {
    width: 100%;
  }
  .heading {
    padding: 20px;
    font-size: 30px;
  }
  .card {
    width: 80%;
  }
}

/* css-side-effects-plugin-ns:app/components/TextBanner/textbanner.css */
.cta-box {
  background: #eceae6;
  border-radius: 2rem;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}
.cta-text {
  padding: 20px;
}
.cta-text h3 span {
  color: var(--heading_color);
  font-size: 1.5rem;
}

/* css-side-effects-plugin-ns:app/components/Portfolio/portfolio.css */
.portfolio__container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2.5rem;
}
.portfolio__item {
  background: var(--color-bg-variant);
  padding: 1.3rem;
  border-radius: 2rem;
  border: 1px solid transparent;
  transition: var(--transition);
}
.portfolio__item:hover {
  border-color: var(--color-primary-variant);
  background: transparent;
}
.portfolio__item-image {
  border-radius: 1.5rem;
  overflow: hidden;
  height: 30%;
}
img {
  display: block;
  width: 100%;
  object-fit: cover;
}
.portfolio__item h3 {
  margin: 2rem 0;
}
.portfolio__item p {
  margin: 0 0 1.2rem 0;
  font-size: 0.9rem;
}
.portfolio__item-cta {
  display: flex;
  width: 100%;
  gap: 1rem;
  margin-bottom: 7rem;
  margin-left: auto;
  margin-right: auto;
}
.portfolio__item-content {
  height: 40%;
}
@media screen and (max-width: 1024px) {
  .portfolio__container {
    grid-template-columns: 1fr 1fr;
    gap: 1.2rem;
  }
}
@media screen and (max-width: 600px) {
  .portfolio__container {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
}

/* css-side-effects-plugin-ns:app/components/SectionContainer/SectionContainer.css */

/* css-side-effects-plugin-ns:app/components/Faq/faq.css */
.accordion {
  max-width: 80%;
  margin: 1rem auto;
}
.accordion-title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  cursor: pointer;
  border-bottom: 1px solid var(--heading_color);
}
.accordion-title:hover {
  background-color: var(--light_color);
}
.accordion-title,
.accordion-content {
  padding: 1rem;
}
.accordion-content {
  background-color: var(--light_color);
  border-bottom: 1px solid var(--heading_color);
}
@media screen and (max-width: 600px) {
  .accordion {
    width: 90%;
  }
}

/* css-side-effects-plugin-ns:app/components/AboutCard/aboutcard.css */
.row {
  margin-left: 2rem;
}
.about-container {
  max-width: 1100px;
  margin: 5rem auto;
  border: 1px solid #000;
}
.about-section {
  padding: 50px;
  text-align: center;
  background-image: url(/build/_assets/aboutus1-STZFD47T.jpg);
  background-size: 100% 100%;
  background-position: 0% 0%;
  height: 300px;
  color: white;
  margin-bottom: 20px;
}
.about-section h2 {
  color: white;
  font-size: 3rem;
  font-weight: 900;
}
.about-section h3 {
  color: white;
  font-size: 2rem;
  font-weight: 300;
  margin-top: 2rem;
}
